import React, { useState, useRef, useEffect, useCallback } from 'react';
import {
  Box,
  Container,
  TextField,
  IconButton,
  Typography,
  Paper,
  Avatar,
  CircularProgress,
  Stack,
  useTheme,
  AppBar,
  Toolbar,
  Tooltip,
  useMediaQuery,
} from '@mui/material';
import {
  Send as SendIcon,
  SmartToy as BotIcon,
  Person as PersonIcon,
  MoreVert as MoreIcon,
} from '@mui/icons-material';

const API_BASE_URL = 'https://phosai-dbec65d5-46be-45ae-9062.cranecloud.io';

const MessageBubble = ({ message, theme }) => {
  const isUser = message.role === 'user';
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  
  const formatTimestamp = (timestamp) => {
    return new Intl.DateTimeFormat('en-US', {
      hour: 'numeric',
      minute: 'numeric',
      hour12: true
    }).format(new Date(timestamp));
  };

  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: isUser ? 'flex-end' : 'flex-start',
        mb: 2,
        maxWidth: '100%',
      }}
    >
      {!isUser && (
        <Avatar
          sx={{
            bgcolor: theme.palette.primary.main,
            mr: 1,
            width: isMobile ? 28 : 32,
            height: isMobile ? 28 : 32,
          }}
        >
          <BotIcon sx={{ fontSize: isMobile ? 16 : 20 }} />
        </Avatar>
      )}
      
      <Paper
        elevation={1}
        sx={{
          p: isMobile ? 1.5 : 2,
          maxWidth: isMobile ? '80%' : '70%',
          borderRadius: 2,
          bgcolor: isUser ? 'primary.main' : 'background.paper',
          color: isUser ? 'common.white' : 'text.primary',
        }}
      >
        <Typography 
          variant={isMobile ? "body2" : "body1"} 
          sx={{ wordBreak: 'break-word' }}
        >
          {message.content}
        </Typography>
        <Typography
          variant="caption"
          sx={{
            display: 'block',
            mt: 0.5,
            color: isUser ? 'rgba(255,255,255,0.7)' : 'text.secondary',
          }}
        >
          {formatTimestamp(message.timestamp)}
        </Typography>
      </Paper>

      {isUser && (
        <Avatar
          sx={{
            bgcolor: 'grey.400',
            ml: 1,
            width: isMobile ? 28 : 32,
            height: isMobile ? 28 : 32,
          }}
        >
          <PersonIcon sx={{ fontSize: isMobile ? 16 : 20 }} />
        </Avatar>
      )}
    </Box>
  );
};

const Chats = ({ agentId, targetLang = 'en', botName = 'My Resume' }) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const chatContainerRef = useRef(null);
  const messagesEndRef = useRef(null);

  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [isTyping, setIsTyping] = useState(false);
  const [streamingText, setStreamingText] = useState('');
  const [error, setError] = useState(null);

  const scrollToBottom = useCallback((smooth = true) => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({
        behavior: smooth ? 'smooth' : 'auto',
        block: 'end',
      });
    }
  }, []);

  useEffect(() => {
    scrollToBottom();
  }, [messages, scrollToBottom]);

  // Ensure chat container takes remaining height
  useEffect(() => {
    const resizeContainer = () => {
      if (chatContainerRef.current) {
        const windowHeight = window.innerHeight;
        const headerHeight = 64; // AppBar height
        const inputHeight = 76; // Input area height
        const availableHeight = windowHeight - headerHeight - inputHeight;
        chatContainerRef.current.style.height = `${availableHeight}px`;
      }
    };

    resizeContainer();
    window.addEventListener('resize', resizeContainer);
    return () => window.removeEventListener('resize', resizeContainer);
  }, []);

  const streamResponse = async (response) => {
    setIsTyping(true);
    let streamedText = '';
    const words = response.split(' ');
    
    for (let word of words) {
      streamedText += word + ' ';
      setStreamingText(streamedText);
      await new Promise(resolve => setTimeout(resolve, Math.random() * 50 + 30));
    }
    
    setIsTyping(false);
    setStreamingText('');
    return streamedText.trim();
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!input.trim() || isLoading) return;

    const userMessage = {
      role: 'user',
      content: input.trim(),
      timestamp: new Date().toISOString()
    };

    setMessages(prev => [...prev, userMessage]);
    setInput('');
    setIsLoading(true);
    setError(null);
    scrollToBottom(false);

    try {
      const formData = new FormData();
      formData.append('agent_id', agentId);
      formData.append('query', userMessage.content);
      formData.append('target_lang', targetLang);

      console.log('Sending request to:', `${API_BASE_URL}/agents/conversations`);
      console.log('FormData:', {
        agent_id: agentId,
        query: userMessage.content,
        target_lang: targetLang
      });

      const response = await fetch(`${API_BASE_URL}/agents/conversations`, {
        method: 'POST',
        body: formData,
        headers: {
          'Accept': 'application/json',
        },
        credentials: 'include',
      });

      if (!response.ok) {
        throw new Error(`Server error: ${response.status}`);
      }

      const data = await response.json();
      console.log('API Response:', data);
      
      if (data?.answer) {
        const streamedResponse = await streamResponse(data.answer);
        const botMessage = {
          role: 'assistant',
          content: streamedResponse,
          timestamp: new Date().toISOString()
        };
        setMessages(prev => [...prev, botMessage]);
      } else {
        throw new Error('Invalid response format from server');
      }
    } catch (error) {
      console.error('API Error:', error);
      setError(error.message);
      
      // Add error message to chat
      const errorMessage = {
        role: 'assistant',
        content: 'Sorry, I encountered an error processing your request. Please try again.',
        timestamp: new Date().toISOString()
      };
      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        height: '100vh',
        overflow: 'hidden',
        bgcolor: 'grey.50',
      }}
    >
      <AppBar position="static" elevation={2}>
        <Toolbar sx={{ minHeight: { xs: 56, sm: 64 } }}>
          <Avatar 
            sx={{ 
              bgcolor: 'primary.dark',
              mr: 2,
              width: isMobile ? 32 : 40,
              height: isMobile ? 32 : 40,
              border: '2px solid rgba(255, 255, 255, 0.2)',
            }}
          >
            <BotIcon sx={{ fontSize: isMobile ? 20 : 24 }} />
          </Avatar>
          <Box sx={{ flexGrow: 1 }}>
            <Typography variant={isMobile ? "subtitle1" : "h6"} sx={{ fontWeight: 600 }}>
              {botName}
            </Typography>
            <Typography variant="caption" sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>
              AI Powered Assistant
            </Typography>
          </Box>
          <Tooltip title="Options">
            <IconButton color="inherit">
              <MoreIcon />
            </IconButton>
          </Tooltip>
        </Toolbar>
      </AppBar>

      <Box
        ref={chatContainerRef}
        sx={{
          flex: 1,
          overflowY: 'auto',
          px: { xs: 1, sm: 2 },
          py: { xs: 2, sm: 3 },
          '&::-webkit-scrollbar': {
            width: '8px',
            background: 'transparent',
          },
          '&::-webkit-scrollbar-thumb': {
            background: theme.palette.grey[300],
            borderRadius: '4px',
            '&:hover': {
              background: theme.palette.grey[400],
            },
          },
        }}
      >
        <Container maxWidth="md">
          {messages.length === 0 ? (
            <Box
              sx={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                justifyContent: 'center',
                minHeight: isMobile ? '60vh' : '50vh',
                p: 2,
              }}
            >
              <Avatar
                sx={{
                  width: isMobile ? 64 : 80,
                  height: isMobile ? 64 : 80,
                  bgcolor: 'primary.light',
                  mb: 2,
                }}
              >
                <BotIcon sx={{ fontSize: isMobile ? 32 : 40 }} />
              </Avatar>
              <Typography variant={isMobile ? "h6" : "h5"} gutterBottom align="center">
                Welcome to {botName}
              </Typography>
              <Typography 
                variant="body2" 
                color="text.secondary" 
                align="center"
                sx={{ px: isMobile ? 2 : 0 }}
              >
                Start a conversation by typing a message below
              </Typography>
            </Box>
          ) : (
            <Stack spacing={isMobile ? 1 : 2}>
              {messages.map((message, index) => (
                <MessageBubble key={index} message={message} theme={theme} />
              ))}
              {isTyping && (
                <MessageBubble
                  message={{
                    role: 'assistant',
                    content: streamingText || '...',
                    timestamp: new Date().toISOString(),
                  }}
                  theme={theme}
                />
              )}
            </Stack>
          )}
          <div ref={messagesEndRef} />
        </Container>
      </Box>

      <Paper
        component="form"
        onSubmit={handleSubmit}
        elevation={3}
        sx={{
          p: { xs: 1.5, sm: 2 },
          bgcolor: 'background.paper',
          borderTop: '1px solid',
          borderColor: 'divider',
        }}
      >
        <Container maxWidth="md">
          <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
            <TextField
              fullWidth
              variant="outlined"
              placeholder="Type your message..."
              value={input}
              onChange={(e) => setInput(e.target.value)}
              disabled={isLoading}
              size="small"
              sx={{
                '& .MuiOutlinedInput-root': {
                  borderRadius: 2,
                }
              }}
            />
            <IconButton
              type="submit"
              disabled={!input.trim() || isLoading}
              sx={{
                bgcolor: input.trim() ? 'primary.main' : 'grey.300',
                color: 'white',
                '&:hover': {
                  bgcolor: input.trim() ? 'primary.dark' : 'grey.400',
                },
                transition: 'all 0.2s',
                width: { xs: 36, sm: 40 },
                height: { xs: 36, sm: 40 },
                '&:disabled': {
                  bgcolor: 'grey.300',
                }
              }}
            >
              {isLoading ? (
                <CircularProgress size={isMobile ? 20 : 24} color="inherit" />
              ) : (
                <SendIcon sx={{ fontSize: isMobile ? 20 : 24 }} />
              )}
            </IconButton>
          </Box>
        </Container>
      </Paper>
    </Box>
  );
};

export default Chats;